import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';


// material-ui
import { makeStyles } from '@material-ui/styles';
import {
    Box,
    Button,
    Checkbox,
    FormControl,
    FormControlLabel,
    FormHelperText,
    Grid,
    IconButton,
    InputAdornment,
    InputLabel,
    OutlinedInput,
    Typography
} from '@material-ui/core';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';
import useAuth from 'hooks/useAuth';
import useScriptRef from 'hooks/useScriptRef';

// assets
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

import loader from 'assets/images/ring-loader.svg';


// style constant
const useStyles = makeStyles((theme) => ({
    loginInput: {
        ...theme.typography.customInput
    }
}));

// ===============================|| JWT LOGIN ||=============================== //

const JWTCustomerLogin = ({ loginIndex, ...others }) => {
    const classes = useStyles();
    //const { login } = useAuth();
    const { loginCustomer, isLoading } = useAuth(); 

    const scriptedRef = useScriptRef();

    const [checked, setChecked] = React.useState(true);

    const [showPassword, setShowPassword] = React.useState(false);
    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    return (
        <Formik
            /* initialValues={{
                email: 'info@codedthemes.com',
                password: '123456',
                submit: null
            }} */
            validationSchema={Yup.object().shape({
                /* email: Yup.string().email('Must be a valid email').max(255).required('Email is required'), */
                password: Yup.string().max(255).required('Password is required')
            })}
            /* onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
                try {
                    await login(values.email, values.password);

                    if (scriptedRef.current) {
                        setStatus({ success: true });
                        setSubmitting(false);
                    }
                } catch (err) {
                    console.error(err);
                    if (scriptedRef.current) {
                        setStatus({ success: false });
                        setErrors({ submit: err.message });
                        setSubmitting(false);
                    }
                }
            }} */
        >
            {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
                <form noValidate onSubmit={loginCustomer/* handleSubmit */} {...others}>
                    <FormControl fullWidth /* error={Boolean(touched.email && errors.email)} */ className={classes.loginInput}>
                        <InputLabel htmlFor="outlined-adornment-email-login">Username</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-email-login"
                            type="text"
                            /* value={values.username} */
                            name="username"
                            onBlur={handleBlur}
                            /* onChange={handleChange} */
                            inputProps={{
                                classes: {
                                    notchedOutline: classes.notchedOutline
                                }
                            }}
                        />
                        {/* {touched.email && errors.email && (
                            <FormHelperText error id="standard-weight-helper-text-email-login">
                                {' '}
                                {errors.email}{' '}
                            </FormHelperText>
                        )} */}
                    </FormControl>

                    <FormControl fullWidth /* error={Boolean(touched.password && errors.password)} */ className={classes.loginInput}>
                        <InputLabel htmlFor="outlined-adornment-password-login">Password</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password-login"
                            type={showPassword ? 'text' : 'password'}
                            /* value={values.password} */
                            name="password"
                            onBlur={handleBlur}
                            /* onChange={handleChange} */
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            }
                            inputProps={{
                                classes: {
                                    notchedOutline: classes.notchedOutline
                                }
                            }}
                            label="Password"
                        />
                        {/* {touched.password && errors.password && (
                            <FormHelperText error id="standard-weight-helper-text-password-login">
                                {' '}
                                {errors.password}{' '}
                            </FormHelperText>
                        )} */}
                    </FormControl>

                    <Grid container alignItems="center" justifyContent="space-between">
                        <Grid item>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={checked}
                                        onChange={(event) => setChecked(event.target.checked)}
                                        name="checked"
                                        color="primary"
                                    />
                                }
                                label="Keep me logged in"
                            />
                        </Grid>
                        <Grid item>
                            <Typography
                                variant="subtitle1"
                                component={Link}
                                to={
                                    loginIndex
                                        ? `/pages/forgot-password/forgot-password${loginIndex}`
                                        : '/pages/forgot-password/forgot-password1'
                                }
                                color="secondary"
                                sx={{ textDecoration: 'none' }}
                            >
                                Forgot Password?
                            </Typography>
                        </Grid>
                    </Grid>

                    {errors.submit && (
                        <Box
                            sx={{
                                mt: 3
                            }}
                        >
                            <FormHelperText error>{errors.submit}</FormHelperText>
                        </Box>
                    )}
                    <Box
                        sx={{
                            mt: 2
                        }}
                    >
                        <AnimateButton>
                            {isLoading ? <Button color="secondary" disabled={isSubmitting} fullWidth size="large" type="submit" variant="contained">Signing In     <img src={loader} alt="" style={{height: '40px', width: '40px'}}/> </Button>:<Button color="secondary" disabled={isSubmitting} fullWidth size="large" type="submit" variant="contained">
                                Sign In
                            </Button>}
                            
                        </AnimateButton>
                    </Box>
                </form>
            )}
        </Formik>
    );
};

JWTCustomerLogin.propTypes = {
    loginIndex: PropTypes.number
};

export default JWTCustomerLogin;
